import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { PagingModel } from '../../../_base/models/response-model';
import { ExampleCrudService } from '../../../_share/services/example/example-crud-service.service';
import { TableConfigModel } from '../../../_base/models/table-config-model';
import { DialogService, DialogSize } from '../../../_base/services/dialog.service';
import { CrudDialogComponent } from './crud-dialog/crud-dialog.component';

interface UserModel {
  id: string;
  fullName: string;
  age: number;
  birthDate: string;
  level: number;
}

@Component({
  selector: 'app-crud',
  templateUrl: './crud.component.html',
  styleUrls: ['./crud.component.scss']
})
export class CrudComponent implements OnInit {

  formSearch: FormGroup;
  listOfData: UserModel[] = [];
  isLoading = false;

  public tableConfig: TableConfigModel = new TableConfigModel({
    keyId: 'id',
    isAllChecked: false,
    indeterminate: false,
    itemSelected: new Set<any>()
  });

  public paging: PagingModel = {
    count: 0,
  };

  constructor(
    private fb: FormBuilder,
    private exampleCrudService: ExampleCrudService,
    private dialogService: DialogService
  ) { }

  async ngOnInit(): Promise<void> {
    this.formSearch = this.fb.group({
      id: [null],
      fullName: [null],
    });
    await this.getData();
  }

  async getData(paging: PagingModel = {page: 1, size: 10}): Promise<void> {
    this.isLoading = true;
    const rs = await this.exampleCrudService.getPaging<any>({
      page: paging.page,
      size: paging.size
    });
    if (rs.ok) {
      console.log(rs);
      this.listOfData = rs.result.data;
      this.paging = {
        ...this.paging,
        size: paging.size,
        page: paging.page,
        count: rs.result.paging.count
      };
      console.log(this.paging);
    }
    this.isLoading = false;
  }

  show(id: string): void {

  }

  edit(id: any): void {

  }

  delete(param: any[]): void {

  }

  async addDataDialog(): Promise<void> {
    const dialog = this.dialogService.openDialog(options => {
      options.title = 'Example add';
      options.size = DialogSize.full;
      options.component = CrudDialogComponent;
      options.inputs = {

      };
    }, (eventName, eventValue) => {
      if (eventName === 'onClose') {
        this.dialogService.closeDialogById(dialog.id);
      }
    });
  }

  async editBtn(): Promise<void> {

  }
}
